import React, { useEffect, useRef, useState } from 'react';
import { getTemplate } from './api/Api';
import Handlebars from "handlebars";
import TemplateComponent from './helper/TemplateComponent';

function App({ name, nameTwo, templateId }) {

    const [css] = useState(
        `#wrapper {
          background:red;
          padding:10px;
        }`
    ); // compiled scss

    const [html, setHtml] = useState(`
      <div id="wrapper">
        <h1 class="title">{{name}}</h1>
      </div>
      `); // template

    useEffect(() => {
        (async () => {
            const { data } = await getTemplate('code', '1002');
            // setTemplate(data[0].contentshape);
            const hbr = `${data[0].contentshape}`;
            setHtml(hbr);
            // const template = Handlebars.compile(hbr);
            // setAfterTranspile(template({ name: "Vijay Yadav" }))
            // Ref.current.innerHTML = template({ name: "Vijay Yadav" });
        })()
    }, [])

    const data = {
        name: "VIJAY",
        bold: function () {
            return function (text, render) {
                return;
            };
        },
        bannerType: "wideBanner"
    };
    return (
        <div>
            <TemplateComponent css={css} template={html} data={data} />
        </div>
    );
}

export default App;
